    background-image: url(/craeft/static/media/progress.56b50d07.png);
    background-repeat: repeat-x;
    background-size: 36px 100%;
}

/* progress bar - blue color */
.rpgui-progress-fill.blue {
    background-image: url(/craeft/static/media/progress-blue.8fef7291.png);
