/**
* style for radioes
*/
/* radio box */
.rpgui-content input[type=radio].rpgui-radio {
    display: none;
}

.rpgui-content input[type=radio].rpgui-radio + label {
    background: url(/craeft/static/media/radio-off.31720e09.png) no-repeat;
    line-height: 24px;
    display: inline-block;
    background-size: auto 100%;
    padding-left: 34px;
    height: 24px;
    margin-top: 8px;
    margin-bottom: 8px;
}

.rpgui-content input[type=radio].rpgui-radio:checked + label {
    background: url(/craeft/static/media/radio-on.287354ad.png) no-repeat;
    line-height: 24px;
    display: inline-block;
    background-size: auto 100%;
    padding-left: 34px;
    height: 24px;
}

/* golden radio */
.rpgui-content .rpgui-radio.golden + label {
    background: url(/craeft/static/media/radio-golden-off.93eeffc4.png) no-repeat !important;
