/**
* Rules for the slider.
*/
/* regular slider stuff */
/* slider container */
.rpgui-slider-container {
    height: 20px;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
    position: relative;
}

/* slider left edge */
.rpgui-slider-left-edge {
    position: absolute;
    height: 20px;
    width: 20px;
    left: 0px;
    background-image: url(/craeft/static/media/slider-left.ec4d5a68.png);
    background-size: 100% 100%;
}

/* slider right edge */
.rpgui-slider-right-edge {
    position: absolute;
    height: 20px;
    width: 20px;
    right: 0px;
    background-image: url(/craeft/static/media/slider-right.fc7c59d9.png);
    background-size: 100% 100%;
}

/* slider background track */
.rpgui-slider-track {
    position: absolute;
    height: 20px;
    left: 0;
    right: 0;
    background-image: url(/craeft/static/media/slider-track.94019479.png);
    background-repeat: repeat-x;
    background-size: 24px 100%;
}

/* the part of the slider that moves and indicates the value */
.rpgui-slider-thumb {
    position: absolute;
    height: 30px;
    width: 15px;
    margin-top: -5px;
    left: 40px;
    background-image: url(/craeft/static/media/slider-thumb.ca329174.png);
    background-size: 100% 100%;
}

/* golden slider stuff */
/* golden slider container */
.rpgui-slider-container.golden {
    height: 30px;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
    position: relative;
}

/* golden slider left edge */
.rpgui-slider-left-edge.golden {
    position: absolute;
    height: 30px;
    width: 30px;
    left: 0px;
    background-image: url(/craeft/static/media/slider-left-golden.61e770ac.png);
    background-size: 100% 100%;
}

/* golden slider right edge */
.rpgui-slider-right-edge.golden {
    position: absolute;
    height: 30px;
    width: 30px;
    right: 0px;
    background-image: url(/craeft/static/media/slider-right-golden.3be90faf.png);
    background-size: 100% 100%;
}

/* golden slider background track */
.rpgui-slider-track.golden {
    position: absolute;
    height: 30px;
    left: 0;
    right: 0;
    background-image: url(/craeft/static/media/slider-track-golden.c99dc351.png);
    background-repeat: repeat-x;
    background-size: 40px 100%;
}

/* golden the part of the slider that moves and indicates the value */
.rpgui-slider-thumb.golden {
    position: absolute;
    height: 36px;
    width: 18px;
    margin-top: -4px;
    left: 40px;
    background-image: url(/craeft/static/media/slider-thumb-golden.1207b17e.png);
    background-size: 100% 100%;
}

* {
    font-family: 'MedievalSharp', cursive;
    font-size: 24px;
}

html, body {
    color: #FAEBD7;
    background-color: #000;
}

strong {
    color: #FAEFE1;
}

.craeft-logo {
    font-size: 90px;
    font-weight: bold;
    color: #FAEFE1;
}

.column {
    display: inline-block;
    vertical-align: top;
    padding-right: 20px;
}

.frame {
    min-width: 380px;
}

.row {
    text-align: center;
}

.player {
    margin-bottom: 30px;
}

.player hr {
    margin-bottom: 18px;
}

.item,
.resources,
.craefter,
.craeft-window,
.craefter-window {
    border: 1px solid #FAEFE1;
    margin-bottom: 5px;
    margin-top: 2px;
    padding: 6px;
}

.item-level {
    position: relative;
    left: 10px;
    top: -5px;
    font-size: 18px!important;
}

.item-timeout {
    position: relative;
    left: 15px;
    top: 25px;
}

.craefters-list {
    margin-top: 5px;
}

.items hr,
.craefters hr,
.farm hr {
    margin-top: 10px;
    margin-bottom: 0;
}

.footer {
    text-align: center;
    background-color: #000;
}

.item-description {
    min-width: 80%;
}
